
  /* Layout */
  .container-course {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 1rem;
  }
  
  .min-h-screen {
    min-height: 100vh;
  }
  
  .bg-gray-50 {
    background-color: #f9fafb;
  }
  
  /* Flexbox and Grid */
  .flex {
    display: flex;
  }
  
  .flex-wrap {
    flex-wrap: wrap;
  }
  
  .items-center {
    align-items: center;
  }
  
  .justify-center {
    justify-content: center;
  }
  
  .grid {
    display: grid;
  }
  
  .gap-4 {
    gap: 1rem;
  }
  
  .gap-6 {
    gap: 1.5rem;
  }
  
  /* Spacing */
  .px-4 {
    padding-left: 1rem;
    padding-right: 1rem;
  }
  
  .py-2 {
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
  }
  
  .py-4 {
    padding-top: 1rem;
    padding-bottom: 1rem;
  }
  
  .py-8 {
    padding-top: 2rem;
    padding-bottom: 2rem;
  }
  
  .py-12 {
    padding-top: 3rem;
    padding-bottom: 3rem;
  }
  
  .p-4 {
    padding: 1rem;
  }
  
  .p-6 {
    padding: 1.5rem;
  }
  
  .p-8 {
    padding: 2rem;
  }
  
  .mt-2 {
    margin-top: 0.5rem;
  }
  
  .mt-4 {
    margin-top: 1rem;
  }
  
  .mt-8 {
    margin-top: 2rem;
  }
  
  .mb-2 {
    margin-bottom: 0.5rem;
  }
  
  .mb-4 {
    margin-bottom: 1rem;
  }
  
  .mr-1 {
    margin-right: 0.25rem;
  }
  
  .mr-2 {
    margin-right: 0.5rem;
  }
  
  .mx-2 {
    margin-left: 0.5rem;
    margin-right: 0.5rem;
  }
  
  /* Typography */
  .text-xs {
    font-size: 0.75rem;
  }
  
  .text-sm {
    font-size: 0.875rem;
  }
  
  .text-lg {
    font-size: 1.125rem;
  }
  
  .text-xl {
    font-size: 1.25rem;
  }
  
  .text-2xl {
    font-size: 1.5rem;
  }
  
  .text-4xl {
    font-size: 2.25rem;
  }
  
  .font-medium {
    font-weight: 500;
  }
  
  .font-semibold {
    font-weight: 600;
  }
  
  .font-bold {
    font-weight: 700;
  }
  
  .text-center {
    text-align: center;
  }
  
  /* Colors */
  .text-white {
    color: #ffffff;
  }
  
  .text-gray-500 {
    color: #6b7280;
  }
  
  .text-gray-600 {
    color: #4b5563;
  }
  
  .text-gray-700 {
    color: #374151;
  }
  
  .text-navy-800 {
    color: #1e3a8a;
  }
  
  .text-blue-600 {
    color: #2563eb;
  }
  
  .text-green-600 {
    color: #16a34a;
  }
  
  .bg-white {
    background-color: #ffffff;
  }
  
  .bg-blue-600 {
    background-color: #2563eb;
  }
  
  .bg-blue-50 {
    background-color: #eff6ff;
  }
  
  .bg-orange-600 {
    background-color: #ea580c;
  }
  
  .bg-gray-500 {
    background-color: #6b7280;
  }
  
  /* Borders */
  .border {
    border: 1px solid #e5e7eb;
  }
  
  .border-b {
    border-bottom: 1px solid #e5e7eb;
  }
  
  .border-b-2 {
    border-bottom-width: 2px;
  }
  
  .border-blue-600 {
    border-color: #2563eb;
  }
  
  .rounded-md {
    border-radius: 0.375rem;
  }
  
  .rounded-lg {
    border-radius: 0.5rem;
  }
  
  /* Effects */
  .shadow-sm {
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  }
  
  .shadow-md {
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  }
  
  /* Transitions */
  .transition-all {
    transition-property: all;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 150ms;
  }
  
  .transition-colors {
    transition-property: color, background-color, border-color;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 150ms;
  }
  
  /* Hover states */
  .hover\:shadow-md:hover {
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  }
  
  .hover\:bg-blue-50:hover {
    background-color: #eff6ff;
  }
  
  /* Specific components */
  .breadcrumb {
    display: flex;
    align-items: center;
    font-size: 0.875rem;
    color: #4b5563;
  }
  
  .hero-section {
    padding: 2rem 1rem;
  }
  
  .hero-title {
    font-size: 2.25rem;
    font-weight: 700;
    color: #1e3a8a;
  }
  
  .hero-subtitle {
    font-size: 1.25rem;
    color: #374151;
    margin-top: 0.5rem;
  }
  
  .category-grid {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(1, 1fr);
  }
  
  .category-card {
    padding: 1rem;
    border-radius: 0.375rem;
    border: 1px solid #e5e7eb;
    cursor: pointer;
    transition: all 150ms;
  }
  
  .category-card:hover {
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  }
  
  .category-card.active {
    background-color: #2563eb;
    color: white;
  }
  
  .tab-navigation {
    display: flex;
    flex-wrap: wrap;
    border-bottom: 1px solid #e5e7eb;
  }
  
  .tab-button {
    padding: 0.5rem 1rem;
    font-weight: 500;
    color: #4b5563;
    background: none;
    border: none;
    cursor: pointer;
  }
  
  .tab-button.active {
    color: #2563eb;
    border-bottom: 2px solid #2563eb;
  }
  
  .course-features {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    font-size: 0.875rem;
    color: #374151;
    gap: 1.5rem;
  }
  
  .feature-check {
    color: #16a34a;
    margin-right: 0.25rem;
  }
  
  .course-grid {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 1.5rem;
  }
  
  .course-card {
    background-color: white;
    border-radius: 0.5rem;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
    overflow: hidden;
  }
  
  .course-tag {
    color: white;
    font-size: 0.75rem;
    font-weight: 500;
    padding: 0.25rem 0.75rem;
    text-align: center;
  }
  
  .tag-role-combo {
    background-color: #ea580c;
  }
  
  .tag-authorized {
    background-color: #6b7280;
  }
  
  .tag-other {
    background-color: #2563eb;
  }
  
  .course-content {
    padding: 1.5rem;
  }
  
  .course-title {
    font-size: 1.25rem;
    font-weight: 700;
    color: #1e3a8a;
    margin-bottom: 0.5rem;
  }
  
  .course-description {
    color: #4b5563;
    margin-bottom: 1rem;
  }
  
  .course-meta {
    display: flex;
    align-items: center;
    margin-bottom: 0.5rem;
    color: #374151;
  }
  
  .meta-icon {
    color: #2563eb;
    margin-right: 0.5rem;
    height: 1.25rem;
    width: 1.25rem;
  }
  
  .course-date {
    color: #16a34a;
    font-size: 0.875rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
  }
  
  .view-details-button {
    width: 100%;
    margin-top: 0.5rem;
    border: 1px solid #2563eb;
    color: #2563eb;
    padding: 0.5rem 1rem;
    border-radius: 0.375rem;
    background: none;
    cursor: pointer;
    transition: background-color 150ms;
  }
  
  .view-details-button:hover {
    background-color: #eff6ff;
  }
  
  .empty-state {
    text-align: center;
    padding: 3rem 0;
  }
  
  .empty-title {
    font-size: 1.25rem;
    font-weight: 500;
    color: #374151;
  }
  
  .empty-message {
    color: #6b7280;
    margin-top: 0.5rem;
  }
  
  /* Media Queries */
  @media (min-width: 640px) {
    .category-grid {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  
  @media (min-width: 768px) {
    .category-grid {
      grid-template-columns: repeat(3, 1fr);
    }
    
    .course-grid {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  
  @media (min-width: 1024px) {
    .category-grid {
      grid-template-columns: repeat(4, 1fr);
    }
    
    .course-grid {
      grid-template-columns: repeat(3, 1fr);
    }
  }